body {
  margin: 40px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #212121;
}

code {
  font-family: JetBrains bold monospace;
}

* {
  color: #FBF1C7;
  margin: 0px;
  padding: 0px;
  font-weight: bold
}

.Wrapper{
  display: grid;
  gap: 50px;
  grid-auto-flow: column;
  margin: 0 0 1rem 0;
}

.Wrapper > div{
  gap: 40px;
  transition: all 0.3s ease-in-out ;
  height: 400px;
  border-radius: 8px;
  background: #282828;
  box-shadow: 0px 0 15px 0 rgba(0,0,0,0.5); 
}

.Wrapper > div:hover{
  transition: all 0.3s ease-in-out ;
  background: #282828;
  transform: scale(102%);
  color: red;
}
